.header {
    padding: 5px;
    background-color: beige;
    font-weight: bold;
}

.header ul {
    list-style: none;
    display: block;
}

.header ul li {
    display: inline;
}

.header ul li a {
    padding: 5px;
}

.message {
    background: aliceblue;
    padding: 20px;
    font-size: 14px;
    font-weight: 500;
    font-family: ariel;
    text-align: center;
}

.last-updated {
    color: green;
    font-weight: bold;
    font-family: monospace;
    padding: 15px;
    font-size: 12px;
}

.footer {
    /* position: fixed; */
    bottom: 0;
    margin: 0;
    text-align: center;
    width: 100%;
    background-color: beige;
    font-weight: bold;
}

.heading {
    margin-left: 10px;
    font-weight: bold;
    font-size: 32px;
    font-family: monospace;
}

.heading-data {
    text-align: center;
    padding: 5px;
    font-family: monospace;
}

.confirmed {
    color: red;
}

.active {
    color: lightcoral;
}

.recovered {
    color: green;
}

.deaths {
    color: #a0a3a7;
}

.count {
    transform: scale(1, 1.2);
    font-size: 30px;
    font-weight: bold;
}

.increment {
    margin-top: -10px;
}

table thead tr th {
    padding: 0.25rem !important;
    font-size: 14px;
    text-align: right;
    background-color: #cccccc;
    color: black;
    font-size: 16px;
    font-family: monospace;
}

table tbody tr td {
    font-size: 14px;
    text-align: right;
    padding: 3px !important;
}

table tbody tr:hover td {
    background-color: rgb(206, 203, 203);
}

table thead tr :nth-child(1), table tbody tr :nth-child(1) {
    text-align: left;
}

table tbody tr:last-child {
    text-align: right;
}

table tbody tr:nth-child(even) {
    background-color: rgb(235, 235, 235);
}

table tbody tr td:nth-child(1) {
    color: black;
    transform: scale(1, 1.1);
}

.delta-confirmed {
    color: red;
    transform: scale(1, 0.8);
    font-size: 15px;
}

.delta-recovered {
    color: green;
    transform: scale(1, 0.8);
    font-size: 15px;
}

.delta-deaths {
    color: #545b62;
    transform: scale(1, 0.8);
    font-size: 15px;
}

.affected-states {
    font-weight: bold;
    color: blue;
    font-family: monospace;
}